@import url('https://fonts.googleapis.com/css2?family=Anta&display=swap');


body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    background-color: #092635;
}

.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px 40px;
    text-align: center;
    background-color: #f9f9f9;
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 1);
}

h1 {
    font-size: 6rem;
    font-family: "Anta" sans-serif;
    font-style: normal;
    margin-bottom: 40px;
    text-align: center;
    color: #f9f9f9;
    font-weight: 700;
}

.search-bar {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
    margin-bottom: 20px;


}

.search-input {
    padding: 10px;
    border: none;
    border-radius: 20px;
    box-shadow: 0 2px 4px rgba(0, 0, 0, 1);
    width: 380px;
    font-size: 16px;
    outline: none;
}

.search-button {
    padding: 10px 11px;
    background-color: #fff;
    color: #092635;
    border: none;
    font-size: 20px;
    border-radius: 50%;
    cursor: pointer;
    display: flex;
    align-items: center;

}

.search-icon {
    font-size: 20px;
    margin-right: 5px;
}

.filters {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 15px;
    font-size: 15px;
    font-family: 'Segeo UI' 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color: #f9f9f9;
    margin-top: 30px;
    font-weight: 600;
    margin-bottom: 20px;

}

#sort-by {
    border-radius: 20px;
    padding: 5px 5px;
    border: none;
    font-weight: 600;
    font-size: 12px;
    outline: none;
    color: #333;
}

#genre {
    border-radius: 20px;
    padding: 5px 5px;
    border: none;
    font-weight: 600;
    font-size: 12px;
    outline: none;
    color: #333;
}

.movie-wrapper {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 20px;
    padding: 50px 50px;



}

.movie {
    background-color: white;
    padding: 20px;
    height: max-content;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 1);
}

.movie img {
    width: 100%;
    height: auto;
    border-radius: 8px;
    margin-bottom: 10px;
    box-shadow: 0 6px 15px -3px rgba(0, 0, 0, 1);
}

.movie h2 {
    color: #092635;
    font-size: 1.5rem;
    margin-top: 5px;
    font-weight: 700;
    margin-bottom: 10px;
}

.movie p {
    font-size: 1rem;
    color: #666;
    margin: 0;
    margin-bottom: 10px;
}

.movie .rating {
    border: 2px solid #092635;
    width: fit-content;
    padding: 5px 10px;
    border-radius: 5px;
    font-weight: 600;
    color: #333;
}

.read-more {
    background-color: #092635;
    color: #f9f9f9;
    margin: 10px 10px 10px 0;
    padding: 5px 10px;
    cursor: pointer;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 7px;
}